            <div class="paginator"> 
                <div></div> <div class="active"></div> <div></div> <div></div>
            </div>
        </div>
        <CategoryWrapper name="Цветы" :positions="flowers"></CategoryWrapper>
        <CategoryWrapper name="Веснушки" :positions="dots"></CategoryWrapper>
        <CategoryWrapper name="Слова" :positions="words"></CategoryWrapper>
        <CategoryWrapper name="Животные" :positions="animals"></CategoryWrapper>
        <CategoryWrapper name="Другое" :positions="other"></CategoryWrapper>
        <CategoryWrapper name="Однушки" :positions="once"></CategoryWrapper>
        <CategoryWrapper name="Наборы" :positions="sets"></CategoryWrapper>
    </div>
</template>

<script>
const axios = require('axios')
import CategoryWrapper from './../components/CategoryWrapper'
export default {
    components: {
        CategoryWrapper
    },
    mounted: function () {
        axios.get('api/positions?show_sale=0&category=FL').then((response) => { this.flowers = response.data })
        axios.get('api/positions?show_sale=0&category=DO').then((response) => { this.dots = response.data })
        axios.get('api/positions?show_sale=0&category=WO').then((response) => { this.words = response.data })
        axios.get('api/positions?show_sale=0&category=AN').then((response) => { this.animals = response.data })
        axios.get('api/positions?show_sale=0&category=OT').then((response) => { this.other = response.data })
        axios.get('api/positions?show_sale=0&category=ON').then((response) => { this.once = response.data })
        axios.get('api/positions?show_sale=0&category=SE').then((response) => { this.sets = response.data })
    },
    data: () => ({
        flowers: [],
        dots: [],
        words: [],
        animals: [],
        other: [],
        once: [],
        sets: []
    })
}
</script>

